import { Label } from "../../../../components";

export function renderStatus(status: string) {
  switch (status) {
    case "approved":
      return <Label title=" " type="success" />;
      break;
    case "pending":
      return <Label title="  " type="warning" />;
      break;
    case "rejected":
      return <Label title=" " type="danger" />;
      break;
    case "cancelled":
      return <Label title=" " type="danger" />;
      break;
    case "completed":
      return <Label title=" " type="success" />;
      break;
    default:
      break;
  }
}
