import { Card, Loading } from "@components";
import { isEqual } from "lodash";
import { ScrollView } from "native-base";
import React, { memo } from "react";
import useSchedule from "../useSchedule";
import WeekDaysChart from "./WeekDayChart";

const Insight = () => {
  const { isLoading, completed } = useSchedule();

  return (
    <Card title=" ">
      {isLoading ? (
        <Loading />
      ) : (
        <ScrollView horizontal showsHorizontalScrollIndicator={false}>
          <Card transparent px={0} centerTitle subtitle="   ">
            <WeekDaysChart appts={completed.total} />
          </Card>
          <Card transparent px={0} centerTitle ml={5} subtitle="">
            <WeekDaysChart appts={completed.total} />
          </Card>
        </ScrollView>
      )}
    </Card>
  );
};

export default memo(Insight, isEqual);
