import { AppStatusType } from "@types";

export default function getStatusColor(status: AppStatusType) {
  switch (status) {
    case "approved":
      return "success";
      break;
    case "rejected":
      return "danger";
      break;
    case "cancelled":
      return "danger";
      break;
    case "pending":
      return "info";
      break;
    case "completed":
      return "success";
      break;
    default:
      return "info";
      break;
  }
}

export function getStatusName(status: AppStatusType) {
  switch (status) {
    case "approved":
      return " ";
      break;
    case "rejected":
      return " ";
      break;
    case "cancelled":
      return " ";
      break;
    case "pending":
      return "  ";
      break;
    case "completed":
      return " ";
      break;
    default:
      return "info";
      break;
  }
}
